
import React, { Component } from 'react';
import Swiper from 'swiper';
import { Navigation, Pagination } from 'swiper';


class Slider extends Component {
  componentDidMount(){
	Swiper.use([Navigation, Pagination]);
	var swiper1 = new Swiper(".mainSwiper", {
		speed: 1200,
		effect: "fade",
		fadeEffect: {
			crossFade: true,
		},
		autoplay: {
			delay: 5000,
		},
		navigation: {
		nextEl: ".swiper-button-next",
		prevEl: ".swiper-button-prev",
		},
		pagination: {
		el: ".swiper-pagination",
		type: "fraction",
		},
		keyboard: true,
	});

	var catergory2=document.getElementsByClassName("category")[0];
	var title=document.querySelectorAll(".category2 .title a");

		for(var i=0; i<title.length; i++){

			title[i].addEventListener("mouseenter",function(e){
				e.target.classList.add("active");
			});
			title[i].addEventListener("mouseleave",function(e){
				e.target.classList.remove("active");
			});
			title[i].addEventListener("focusin",function(e){
				e.target.classList.add("active");
			});
		}
  }
	render() {
		return(
			<div className="slider">
				<div className="swiper-container mainSwiper">
					<div className="swiper-wrapper">
						<div className="swiper-slide">
							<img src="https://Hanbchoi.github.io/react_project1/assets/images/slider1.png" alt="images1" />
							<div className="text">
								<p>[SFF2020] 부산 불꽃축제 페스티벌<br />
									촬영현장 부산
								</p>
								<span>2020.08.21</span>
							</div>
						</div>
						<div className="swiper-slide">
							<img src="https://Hanbchoi.github.io/react_project1/assets/images/slider2.png" alt="images2" />
							<div className="text">
								<p>[SFF2020] 서울 음악축제 페스티벌<br />
									촬영현장 서울
								</p>
								<span>2020.08.21</span>
							</div>
						</div>
						<div className="swiper-slide">
							<img src="https://Hanbchoi.github.io/react_project1/assets/images/slider3.png" alt="images3" />
							<div className="text">
								<p>[SFF2020] 안양 음악콘서트<br />
									촬영현장 안양
								</p>
								<span>2020.08.21</span>
							</div>
						</div>
						<div className="swiper-slide">
							<img src="https://Hanbchoi.github.io/react_project1/assets/images/slider4.png" alt="images4" />
							<div className="text">
								<p>[SFF2020] 부산 불꽃축제 페스티벌<br />
									촬영현장 
								</p>
								<span>2020.08.21</span>
							</div>
						</div>
					</div>
					<div className="swiper-button-prev"></div>
					<div className="swiper-button-next"></div>
					<div className="swiper-pagination"></div>
				</div>
				<div className="scroll_down">
					<img src="https://Hanbchoi.github.io/react_project1/assets/images/scrolldown.png" alt="scroll" />
					<span><img src="https://Hanbchoi.github.io/react_project1/assets/images/shapedown.png" alt="shape" /></span>
				</div>
			</div>
		);
	}
}

export default Slider;

   