
import React, { Component } from 'react';
import Swiper, { Autoplay } from 'swiper';


class Category3 extends Component {
  componentDidMount(){
	Swiper.use([Autoplay]);
	var controller=document.getElementsByClassName("controller")[0];
	var contUl=controller.firstElementChild;
	var [prev, next, pause, play]=contUl.children;

	var subSwiper=new Swiper(".subSwiper", {
		slidesPerView: 4,
		spaceBetween: 65,
		autoplay: {
			delay: 3000, 
		},
	});

	prev.addEventListener("click",function(e){
		e.preventDefault();
		subSwiper.slidePrev();
	});
	next.addEventListener("click",function(e){
		e.preventDefault();
		subSwiper.slideNext();
	});
	pause.addEventListener("click",function(e){
		e.preventDefault();
		subSwiper.autoplay.stop();
	});
	play.addEventListener("click",function(e){
		e.preventDefault();
		subSwiper.autoplay.start();
	});
  }
	render() {
		return(
			<div className="category3" id="ticket">
				<div className="top">
					<div className="top_inner">
						<div className="logo"><a href=""><img src="https://Hanbchoi.github.io/react_project1/assets/images/sns_logo.png" alt="sns_logo" /></a></div>
						<div className="sns">
							<div className="inner">
								<ul>
									<li>
										<a href="/"><img src="https://Hanbchoi.github.io/react_project1/assets/images/youtube2.png" alt="youtube" /><span>유튜브 바로가기</span></a>
									</li>
									<li>
										<a href="/"><img src="https://Hanbchoi.github.io/react_project1/assets/images/blog2.png" alt="blog" /><span>블로그 바로가기</span></a>
									</li>
									<li>
										<a href="/"><img src="https://Hanbchoi.github.io/react_project1/assets/images/naver2.png" alt="naver" /><span>네이버 바로가기</span></a>
									</li>
									<li>
										<a href="/"><img src="https://Hanbchoi.github.io/react_project1/assets/images/facebook2.png" alt="facebook" /><span>페이스북 바로가기</span></a>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
				<div className="ticket">
					<div className="title">
						<p>티켓 구매 바로가기</p>
						<span>2021 Seoul Fringe Festival Online Ticket</span>
					</div>
					<div className="banner">
						<div className="inner">
							<div className="swiper-container subSwiper">
								<div className="swiper-wrapper">
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner1.jpg" alt="banner1" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner2.jpg" alt="banner2" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner3.jpg" alt="banner3" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner4.png" alt="banner4" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner1.jpg" alt="banner5" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner2.jpg" alt="banner6" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner3.jpg" alt="banner7" /></div>
									<div className="swiper-slide"><img src="https://Hanbchoi.github.io/react_project1/assets/images/banner4.png" alt="banner8" /></div>
								</div>
							</div>
							<div className="controller">
								<ul>
									<li><a href="/" className="prev"><img src="https://Hanbchoi.github.io/react_project1/assets/images/btn_prev.png" /></a></li>
									<li><a href="/" className="next"><img src="https://Hanbchoi.github.io/react_project1/assets/images/btn_next.png" /></a></li>
									<li><a href="/" className="pause"><img src="https://Hanbchoi.github.io/react_project1/assets/images/btn_pause.png" /></a></li>
									<li><a href="/" className="play"><img src="https://Hanbchoi.github.io/react_project1/assets/images/btn_play.png" /></a></li>
								</ul>
							</div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Category3;

   